.container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0a0f17;
}



/* Loader */
.spinner {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: aqua;
    display: grid;
    place-items: center;
    animation: zoomin 1.5s infinite alternate-reverse;
    position: relative;
}

.inner {
    background-color: #0a0f17;
    width: 80%;
    height: 80%;
    border-radius: 100%;
}

@keyframes zoomin {
    0% {
        transform: scale(1);
        box-shadow: 0 0 100px 20px rgb(16, 71, 71);
    }

    100% {
        transform: scale(1.5);
        box-shadow: 0 0 100px 20px #000;
    }
}